import React, { useState, useRef } from "react";
import { motion } from "framer-motion";
import { Link } from "gatsby";
import { useLocation } from "@reach/router";
import { ChevronDown, Globe } from "lucide-react";
import data from "../assets/logo.png"

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(null);

  const menuRef = useRef(null);
  const submenuRef = useRef(null);

  const handleDropdownClick = (index) => {
    setDropdownOpen(dropdownOpen === index ? null : index);
  };

  const handleMouseEnter = (index) => {
    setDropdownOpen(index);
  };

  const handleMouseLeave = () => {
    setTimeout(() => {
      if (
        !menuRef.current?.matches(":hover") &&
        !submenuRef.current?.matches(":hover")
      ) {
        setDropdownOpen(null);
      }
    }, 100);
  };

  const navItems = [
    {
      label: "Company",
      subItems: ["About Us", "Careers", "Life at-5C"],
      route: "/company/about-us/",
    },
    {
      label: "AI Products",
      subItems: [
        "Bionic Suite",
        "Prodigi",
        "Osteocheck",
        "Kronos",
        "Protocall",
      ],
      route: "/ai-products",
    },
    { label: "Borderless Radiology", route: "/borderless-radiology" },
    {
      label: "Impact",
      subItems: ["Blogs", "Case Study", "Newsroom"],
      route: "/impact/blogs",
    },
    { label: "Contact Us", route: "/contact-us" },
  ];

  const location = useLocation();

  const isActiveRoute = (item) => {
    if (item.route && location.pathname.includes(item.route)) {
      return true;
    }
    if (item.subItems) {
      return item.subItems.some((subItem) =>
        location.pathname.includes(
          `/${item.label.toLowerCase().replace(" ", "-")}/${subItem
            .toLowerCase()
            .replace(" ", "-")}`
        )
      );
    }
    return false;
  };

  return (
    <header className="bg-white shadow-sm fixed top-0 z-50 w-[100%]">
      <div className="lg:mx-auto px-4 py-3 flex items-center justify-between w-[100%] lg:w-[90%]">
        <Link to="/">
          <motion.img
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
            src="../../static/logo.png"
            alt="5C Logo"
            className="h-12 md:h-[60px]"
          />
        </Link>
        <nav className="hidden md:flex flex-1 justify-center">
          <div className="flex md:space-x-2 lg:space-x-6">
            {navItems.map((item, index) => (
              <div
                key={index}
                className="relative"
                onMouseEnter={() => handleMouseEnter(index)}
                onMouseLeave={handleMouseLeave}
              >
                <motion.div
                  initial={{ opacity: 0, y: -20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                  className={`cursor-pointer px-3 py-2 rounded ${
                    isActiveRoute(item)
                      ? "bg-red-600 text-white"
                      : "text-[#0D50B6]"
                  }`}
                >
                  <Link to={item.route} className="text-[12px] md:text-[14px] lg:text-[16px]">{item.label}</Link>
                  {item.subItems && <ChevronDown className="inline h-4 w-4" />}
                </motion.div>
                {item.subItems && dropdownOpen === index && (
                  <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.3 }}
                    className="absolute bg-white min-w-[150px] text-center shadow-lg p-4 mt-2 left-[-25px] top-[43px] 2xl:top-[50px] rounded-lg z-50"
                    onMouseEnter={() => setDropdownOpen(index)}
                    onMouseLeave={handleMouseLeave}
                    ref={submenuRef}
                  >
                    <ul className="space-y-2">
                      {item.subItems.map((subItem, subIndex) => (
                        <li key={subIndex}>
                          <Link
                            to={`/${item.label
                              .toLowerCase()
                              .replace(" ", "-")}/${subItem
                              .toLowerCase()
                              .replace(" ", "-")}`}
                            className="block transition-colors duration-300 text-[#0D50B6] text-[12px] md:text-[14px] lg:text-[16px]"
                          >
                            {subItem}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  </motion.div>
                )}
              </div>
            ))}
          </div>
        </nav>

        <div className="flex items-center space-x-4 md:block hidden">
          <motion.button
            className="bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700 transition-colors duration-300"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Link to="/sign-in">Sign In</Link>
          </motion.button>
          {/* <motion.button
            className="inline-flex justify-center items-center border border-gray-300 px-2 py-2 gap-2 rounded-md hover:bg-gray-100 transition-colors duration-300"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            Global
            <Globe className="h-5 w-5 mr-0" />
          </motion.button> */}
        </div>

        <div className="md:hidden">
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="text-gray-600 focus:outline-none"
          >
            <svg
              className="h-8 w-8"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d={
                  isMenuOpen
                    ? "M6 18L18 6M6 6l12 12"
                    : "M4 6h16M4 12h16M4 18h16"
                }
              />
            </svg>
          </button>
        </div>
      </div>

      {isMenuOpen && (
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
          className="md:hidden bg-white shadow-lg p-4"
        >
          <ul className="space-y-4">
            {navItems.map((item, index) => (
              <li key={index}>
                <div
                  onClick={() => handleDropdownClick(index)}
                  className="flex justify-between items-center"
                >
                  <Link
                    to={item.route}
                    className="block text-gray-600 hover:text-gray-900 transition-colors duration-300"
                  >
                    {item.label}
                  </Link>
                  {item.subItems && <ChevronDown className="h-4 w-4" />}
                </div>

                {item.subItems && dropdownOpen === index && (
                  <motion.ul
                    initial={{ opacity: 0, y: -10 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.3 }}
                    className="mt-2 ml-4 space-y-2"
                  >
                    {item.subItems.map((subItem, subIndex) => (
                      <li key={subIndex}>
                        <Link
                          to={`/${item.label
                            .toLowerCase()
                            .replace(" ", "-")}/${subItem
                            .toLowerCase()
                            .replace(" ", "-")}`}
                          className="block text-gray-600 hover:text-gray-900 transition-colors duration-300"
                        >
                          {subItem}
                        </Link>
                      </li>
                    ))}
                  </motion.ul>
                )}
              </li>
            ))}
          </ul>

          <div className="mt-4 flex flex-col space-y-2">
            <motion.button
              className="bg-red-600 text-white px-4 py-2 rounded-md hover:bg-red-700 transition-colors duration-300"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Link to="/sign-in">Sign In</Link>
            </motion.button>
            {/* <motion.button
              className="inline-flex justify-center items-center border border-gray-300 px-2 py-2 gap-2 rounded-md hover:bg-gray-100 transition-colors duration-300"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              Global
              <Globe className="h-5 w-5 mr-0" />
            </motion.button> */}
          </div>
        </motion.div>
      )}
    </header>
  );
};

export default Navbar;